import { useForm } from "react-hook-form";
import { object, string } from "yup";
import { yupResolver } from "@hookform/resolvers/yup";
import { texts } from "../../../../texts";
import { FormStep1Values, Sex } from "../../../../types";
import { useAppSelector } from "../../../../store/store";

const { nickname, name, sername, sex } = texts.Errors.Step1;

const formSchema = object().shape({
  nickname: string()
    .required(nickname.required)
    .matches(
      /^[a-zA-Z--0-9]+$/,
      "Nickname      "
    )
    .max(30, "  Nickname - 30 "),
  name: string()
    .required(name.required)
    .matches(/^[a-zA-Z--]+$/, "Name    ")
    .max(50, "  Name - 50 "),
  sername: string()
    .required(sername.required)
    .matches(/^[a-zA-Z--]+$/, "Sername    ")
    .max(50, "  Sername - 50 "),
  sex: string()
    .oneOf([Sex.Man, Sex.Woman], "  ")
    .required("  "),
});

export const useFormStep1 = (
  onSubmit: (formValues: FormStep1Values) => void
) => {
  const userStore = useAppSelector((state) => state.userStore);
  const defaultValues = userStore.formStep1Values;
  const { register, handleSubmit, formState, control } =
    useForm<FormStep1Values>({
      mode: "onTouched",
      // @ts-ignore
      resolver: yupResolver(formSchema),
      defaultValues,
    });

  return {
    register,
    formState,
    control,
    handleSubmit: handleSubmit(onSubmit),
    defaultValues,
  };
};
