import { createAsyncThunk } from "@reduxjs/toolkit";
import { SubmitResponse, UserState } from "../../../types";
import axios from "axios";

export const userAsyncThunk = createAsyncThunk<SubmitResponse, UserState>(
  "user/auth",
  async function (
    { signInFormValues, formStep1Values, formStep2Values, formStep3Values },
    thunkAPI
  ) {
    if (!signInFormValues) {
      return thunkAPI.rejectWithValue(
        "      email"
      );
    }
    if (!formStep1Values) {
      return thunkAPI.rejectWithValue("     1");
    }
    if (!formStep2Values) {
      return thunkAPI.rejectWithValue("     2");
    }
    if (!formStep3Values) {
      return thunkAPI.rejectWithValue("     3");
    }

    try {
      const response = await axios<SubmitResponse>({
        method: "post",
        url: "https://api.sbercloud.ru/content/v1/bootcamp/frontend",
        data: {
          ...signInFormValues,
          ...formStep1Values,
          ...formStep2Values,
          ...formStep3Values,
        },
      });
      return response.data;
    } catch (error) {
      return thunkAPI.rejectWithValue(
        "   .   ."
      );
    }
  }
);
